// Загальний клас для кнопок

.button {
    display: block;
    font-family: inherit;
    cursor: pointer;
    padding: 14px 24px;
        min-width: 236px;
        font-weight: 700;
        text-align: center;
        color: $color-tertiary;
        background-color: rgba(0, 0, 0, 0.4);
        border: 2px solid $color-primary;
            border-radius: 25px;

            &:hover,
                &:focus {
                    color: $color-primary;
                    border-color: $color-tertiary;
                }
}

